}

.footer p {
  font-family: "Raleway", sans-serif;
}

.about {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 100%;
  padding: 5em;
  background-image: url(/OmriGalShen/My-Portfolio/static/media/about_bg_4.8aa558ae.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  /* background-image: linear-gradient(
    121.7deg,
    rgba(41, 116, 250, 1) 22.9%,
    rgba(67, 212, 255, 1) 69%
  ); */
}

.about-panel {
  font-size: 1.5em;
  padding: 2em;
  /* padding-bottom: 8em; */
  width: 70%;
  /* height: 80%; */
}

.about-content {
  /* margin-left: 15%; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.about-title {
  font-family: "Lobster", sans-serif;
}

.about-description p {
  font-family: "Raleway", sans-serif;
}
.about-description {
  width: 60%;
  text-align: left;

  padding-top: 2em;
  padding-bottom: 2em;
}

@media (max-width: 1200px) {
  .about-panel {
    width: 100%;
  }
  .about-description {
    width: 90%;
  }
}

@media (max-width: 600px) {
  .about {
    padding: 0;
  }
  .about-panel {
    width: 100%;
    font-size: 1.2em;
    /* padding-bottom: 50%; */
  }
  .about-description {
    width: 90%;
  }
}

* {
  font-family: "Raleway", sans-serif;
}

.contact {
  width: 100%;
  height: 100%;
  padding: 5em;
  justify-content: center;
  /* text-shadow: 10px 10px 12px black; */
  /* background: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%); */
  background: -webkit-gradient(linear, left top, right top, from(#fc466b), to(#3f5efb));
  background: linear-gradient(90deg, #fc466b 0%, #3f5efb 100%);
  background-image: linear-gradient(
    70.6deg,
    rgba(172, 30, 255, 1) 0.3%,
    rgba(65, 35, 251, 1) 55.8%,
    rgba(35, 251, 224, 1) 105.1%
  );
  /* background-image: radial-gradient(
    circle farthest-corner at 10% 20%,
    rgba(0, 255, 176, 1) 0%,
    rgba(66, 105, 242, 1) 90%
  ); */
  background-image: linear-gradient(
    121.7deg,
    rgba(41, 116, 250, 1) 22.9%,
    rgba(67, 212, 255, 1) 69%
  );
  border: 3px solid black;
  box-shadow: 5px 10px;
  color: white;
  /* text-shadow: 1px 1px 1px white; */
  background-image: url(/OmriGalShen/My-Portfolio/static/media/contact_bg_2.a9f451e9.jpg);
  background-size: cover;
  background-repeat: no-repeat;
}

.grid {
  justify-content: center;
  padding-top: 2em;
