.profile-title {
  font-size: 3rem;
  text-decoration: none;
  font-family: "lobster", sans-serif;
}

.contact-title {
  font-size: 3rem;
  text-decoration: none;
  font-family: "lobster", sans-serif;
}

.profile-pic {
  padding: 1em;
  width: 30%;
  height: auto;
}

.profile-des {
  padding: 1em;
  margin-left: 3em;
  margin-right: 3em;
  font-size: 1.5em;
  font-weight: 400;
  font-family: "Raleway", sans-serif;
}

.contact-title hr {
  border-top: 2px solid;
  width: 50%;
  margin: auto;
}

.contact-list p {
  font-family: "Raleway", sans-serif;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
  color: #3c99eb;
}

.list-item {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  margin: 3em;
  font-size: 1.5em;
}

@media (max-width: 1200px) {
  .contact {
    padding: 0em;
  }
  .profile-pic {
    width: 50%;
  }
  .profile-des {
    margin-left: 1em;
    margin-right: 1em;
    font-size: 1.2rem;
  }
  .contact-list i {
    font-size: 30px;
    padding-right: 1rem;
  }
  .list-item {
    margin: 1.5em;
    font-size: 1.1em;
    padding-left: 1rem;
  }
  .grid {
    padding-bottom: 10%;
  }
  .profile-title {
    font-size: 2rem;
  }

  .contact-title {
    font-size: 2rem;
  }
}
@media (max-width: 350px) {
  .contact-list i {
    font-size: 40px;
    padding-right: 2rem;
  }
  .list-item {
    margin: 1.5em;
    font-size: 1em;
    padding-left: 1rem;
  }
}

.landing {
  text-align: center;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10%;
  background-image: url(/OmriGalShen/My-Portfolio/static/media/tech_bg_4.a99e4889.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  /* background-image: linear-gradient(
    70.6deg,
    rgba(172, 30, 255, 1) 0.3%,
    rgba(65, 35, 251, 1) 55.8%,
    rgba(35, 251, 224, 1) 105.1%
  ); */
}

.title {
  text-decoration: none;
  font-family: "Anton", sans-serif;
}

.banner p {
  font-family: "Raleway", sans-serif;
}

.avatar-pic {
  width: 40%;
  height: auto;
}

hr {
  width: 50%;
}

.banner {
  background-color: black;
  background-image: linear-gradient(
    70.6deg,
    rgba(172, 30, 255, 1) 0.3%,
    rgba(65, 35, 251, 1) 55.8%,
    rgba(35, 251, 224, 1) 105.1%
  );
  border: 5px solid black;
  /* opacity: 0.9; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  padding: 5em;
}

.social-links {
  color: white;
  display: flex;
  justify-content: center;
  width: 50%;
}

.social-links i {
  color: white;
  font-size: 2.5em;
  padding: 12px;
}

@media (max-width: 1000px) {
  .landing {
    padding: 20%;
  }
  .banner {
    padding: 3em;
  }
}

@media (max-width: 600px) {
  .landing {
    padding: 0px;
  }
  .banner {
    padding: 2em;
    padding-bottom: 5em;
  }
  .avatar-pic {
    width: 60%;
  }
}

/* ----------- iPad Pro ----------- */
/* Portrait and Landscape */
@media only screen and (min-width: 1024px) and (max-height: 1366px) and (-webkit-min-device-pixel-ratio: 1.5) {
  .landing {
    padding: 23%;
  }
}

